import {
	getCompany,
} from '@/api/job'
import citys from '@/utils/data'
import industrys from '@/utils/items/industry'
import nature from '@/utils/items/nature'
import size from '@/utils/items/size'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
import {
	mapGetters
} from 'vuex'
export default {
	computed: {
		...mapGetters([
			'address',
		])
	},
	data() {
		return {
			ready: false,
			filter: {
				active: null,
				industry: {
					items: industrys,
					show: false,
					active: 0,
					id: null,
					text: '全部行业'
				},
				nature: {
					items: nature,
					show: false,
					text: null,
					id: null,
				},
				size: {
					items: size,
					show: false,
					text: null,
					id: null,
				},
			},
			locations: {
				province: null,
				city: null,
				area: null,
				citys: '全国',
			},
			list: null,
			count: null,
			search: {
				industry: null,
				nature: 0,
				size: 0,
				value: null,
				count: true,
				page: 1,
				limit: 10
			},
			city: {
				show: false,
				options: citys
			},
			showlist: false
		}
	},
	onShow() {
		this.$store.commit('SET_LTABBAR', 'company');
		this.showlist = true
		this.getCompany();
	},
	onReady() {
		uni.hideTabBar()
		this.ready = true
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	methods: {
		getCompany() {
			const s = this.search
			const t = this
			const a = this.address
			let sl = this.showlist
			const search = Object.assign({}, {
					count: s.count,
					page: sl ? 1 : s.page,
					limit: sl ? 10 * s.page : s.limit,
				},
				s.industry ? {
					industry: s.industry
				} : {},
				s.nature && s.nature > 0 ? {
					nature: s.nature
				} : {},
				s.size && s.size > 0 ? {
					size: s.size
				} : {},
				s.value ? {
					value: s.value
				} : {},
				a.province ? {
					province: a.province
				} : {},
				a.city ? {
					city: a.city
				} : {},
				a.area ? {
					area: a.area
				} : {},
			);
			getCompany(search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.count = r.list.count
					if (t.list && t.list.length > 0 && !sl) {
						t.list = t.list.concat(r.list.rows)
					} else {
						sl = false
						t.list = r.list.rows
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		editCity(e) {
			let s = this.locations
			let address = e.detail.options.map((n) => n.label)
			let arr = e.detail.options.map((n) => n.label)
			if (arr.length === 1) {
				s.citys = address[0]
			} else if (arr.length === 2) {
				s.citys = address[1]
			} else if (arr.length === 3) {
				s.citys = address[2]
			}
			s.province = arr[0]
			s.city = arr[1]
			s.area = arr[2]
			this.$store.commit('SET_ADDRESS', s)
			this.list = null
			this.getCompany()
		},
		refresher() {
			let t = this
			if (t.count > t.list.length) {
				t.search.page = t.search.page++
				this.getCompany()
			} else {
				$wuxToast().hide()
				$wuxToast().show({
					type: 'forbidden',
					text: '已加载全部'
				})
			}
		},
		onClickNav(e) {
			this.filter.industry.active = e.detail.index
		},
		onClickItem(e) {
			this.search.industry = e.detail.id
			const f = this.filter
			f.industry.show = false
			f.active = null
			f.industry.id = e.detail.id
			f.industry.text = e.detail.text
			this.list = null
			this.getCompany()
		},
		filterClick(t, i, n) {
			let p
			if (n === 'nature') {
				p = this.filter.nature
			} else if (n === 'size') {
				p = this.filter.size
			}
			p.text = t
			p.id = i
		},
		upNature() {
			const f = this.filter
			this.search.nature = f.nature.id ? f.nature.id : 0
			f.active = null
			f.nature.show = false
			this.list = null
			this.getCompany()
		},
		upSize() {
			const f = this.filter
			this.search.size = f.size.id ? f.size.id : 0
			f.active = null
			f.size.show = false
			this.list = null
			this.getCompany()
		},
		onChange(e) {
			const d = e.detail
			const f = this.filter
			f.active = d
			f.industry.show = d === 0 ? true : false
			f.nature.show = d === 1 ? true : false
			f.size.show = d === 2 ? true : false
		},
		popupClose(e) {
			const f = this.filter
			f.active = null
			if (e === 'industry') {
				f.industry.show = false
			} else if (e === 'nature') {
				f.nature.show = false
			} else if (e === 'size') {
				f.size.show = false
			}
		},
		detailTo(id) {
			uni.navigateTo({
				url: '/pages/company/detail?id=' + id
			});
		},
		getSearch(e) {
			setTimeout(() => {
				this.search.value = e.detail
				this.list = null
				this.getCompany()
			}, 500)
		},
	}
}
